import { PropType } from 'vue'
import { ClipConfig, ObserverOptions } from './interface'

/**
 * ,  white, gray
 */
export type BackgroundColorEnum = 'white' | 'gray'

/**
 * 
 */
export type LoadingEnum = 'eager' | 'lazy'

export default {
  /**
   *  urlbase64 
   */
  src: {
    type: String,
    required: true,
  },

  /**
   * 
   */
  alt: {
    type: String,
  },

  /**
   * <img/> width
   */
  width: {
    type: [String, Number],
    required: true,
  },

  /**
   * <img/> height
   */
  height: {
    type: [String, Number],
    required: true,
  },

  /**
   *  / 
   */
  aspectRatio: {
    type: Number,
  },

  /**
   * ,  white, gray
   */
  backgroundColor: {
    type: (String as PropType<BackgroundColorEnum>),
  },

  /**
   * trueloading"lazy"
   */
  lazyLoad: {
    type: Boolean,
    default: true,
  },

  /**
   *  0  100 ,  false
   */
  animated: {
    type: Boolean,
    default: false,
  },
  loadingAnimation: {
    type: Boolean,
    default: false,
  },
  enableRem: {
    type: Boolean,
    default: false,
  },
  pure: {
    type: Boolean,
    default: false,
  },
  /**
   * webp
   */
  webp: {
    type: [String, Boolean],
    default: () => true,
  },
  /**
   * loading
   */
  loading: {
    type: (String as PropType<LoadingEnum>),
    validator: (value: string) => ['eager', 'lazy', 'observer'].includes(value),
    default: () => 'lazy',
  },
  /**
   * 
   */
  observerOptions: {
    type: Object as PropType<ObserverOptions>,
  },
  /**
   * 
   */
  clipConfig: {
    type: Object as PropType<ClipConfig>,
    default: () => null,
  },
  /**
   * undefined
   */
  ratio: {
    type: Number,
  },
}
