import { PropType } from 'vue'
import { ButtonSize, ButtonType } from './interface'
/* eslint-disable @typescript-eslint/explicit-module-boundary-types */

export default {
  /**
   * 
   */
  type: {
    type: String as PropType<ButtonType>,
    validator: (value: string) => ['primary', 'outlined', 'text', 'dark', 'filledGray', 'outlinedGray'].includes(value),
    default: 'primary',
  },
  /**
   * 
   */
  size: {
    type: String as PropType<ButtonSize>,
    validator: (value: string) => ['mini', 'small', 'medium', 'large', 'extraLarge'].includes(value),
    default: 'large',
  },

  /**
   * 
   */
  icon: {
    type: String,
    default: '',
  },

  /**
   * 
   */
  active: {
    type: Boolean,
    default: false,
  },

  /**
   * 
   */
  disabled: {
    type: Boolean,
  },

  /**
   * 
   */
  block: {
    type: Boolean,
  },
  /**
   *  
   */
  fixedWidth: {
    type: String,
  },
  loading: {
    type: Boolean,
    default: false,
  },
}
