import { toRem } from '../../../utils/toRem'

function getSizeStyleByProps(width?:string | number, height?:number | string, aspectRatio?:number, enableRem?: boolean, isMounted?: boolean):{
  paddingTop?: string
  width?: string | number
  height?: string | number
} {
  /**
   * widthheightaspectRatio
   */
  if (typeof width === 'string' || typeof height === 'string') {
    return {
      width,
      height,
    }
  }
  /**
   * rem
   */
  if (enableRem && isMounted && typeof width === 'number' && typeof height === 'number') {
    return {
      width: toRem(width),
      height: toRem(height),
    }
  }

  // 
  if (aspectRatio) {
    return {
      /*
      * https://cloud.tencent.com/developer/article/1154233
      * 
      *  2 
      * */
      paddingTop: `${Math.round((1 / aspectRatio) * 10000) / 100}%`,
    }
  }

  if (!width && !height) {
    console.warn('aspectRatioImage  width  height ')
  }
  return { width: width ? `${width}px` : undefined, height: height ? `${height}px` : undefined }
}
export default getSizeStyleByProps
