/* eslint-disable import/no-unresolved */
import {
  DEFAULT_SIGN_HEADER,
  DEFAULT_SIGN_API_PATH,
  encryptToken,
} from '@xhs/fe-api-sign'
import { info } from '@xhs/logger'

// 
const SPAM_CODE = 406
// 406 1
const SIGN_FAIL_ERROR = '1'
//  timestamp 
const TIME_ERROR_CODE = '2'
//  API  406 200 header 
const SIGN_FAIL_CODE = '3'
//  406
const FONT_FAIL_CODE = '4'
// 
const RELOAD_COUNT = 5

function getStorageItem(item) {
  // countStringnull
  const itemCount = parseInt(window.sessionStorage.getItem(item) || 0, 10) + 1
  // setItem
  window.sessionStorage.setItem(item, itemCount.toString())
  // logger 
  info(item, { arg1: itemCount }, item)
  // return reload 
  return itemCount < RELOAD_COUNT
}

function spamNeedReload(kongHeader) {
  let reloadStatus = false
  if (!window.spamTimer) {
    // 
    window.spamTimer = window.setTimeout(() => {
      window.sessionStorage.removeItem('spam_font_counter')
      window.sessionStorage.removeItem('spam_sign_counter')
    }, 3600000)
  }
  if (kongHeader === SIGN_FAIL_CODE || kongHeader === SIGN_FAIL_ERROR) {
    reloadStatus = getStorageItem('spam_sign_counter')
  }
  if (kongHeader === FONT_FAIL_CODE) {
    reloadStatus = getStorageItem('spam_font_counter')
  }
  return reloadStatus
}

export default {
  name: 'crawler-spam',
  installed: false,
  install(app, Launcher) {
    // Server side do not need it 2018112317:53:37
    if (this.installed || !Launcher.isBrowser) {
      return
    }

    Launcher.http.interceptors.dispatch.use(requestConfig => {
      if (requestConfig.url.indexOf(DEFAULT_SIGN_API_PATH) > -1) {
        requestConfig.headers[DEFAULT_SIGN_HEADER] = encryptToken(requestConfig, Launcher.http.buildURL) // eslint-disable-line max-len
      }
      //  sign
      let { url = '' } = requestConfig

      // @ts-ignore
      if (window?.shouldSign && window.shouldSign(url)) {
        let uri = url

        if (url.indexOf('//') === 0) {
          url = `${window.location.protocol}${url}`
        }
        // uri  path url
        if (/^https?:/.test(url)) {
          try {
            const urlObj = new URL(url)

            uri = urlObj.href.replace(urlObj.origin, '')
          } catch (e) {
            uri = url
          }
        }
        const { params, paramsSerializer } = requestConfig
        // @ts-ignore
        const realUrl = Launcher.http.buildURL(uri, params, paramsSerializer)
        //  header
        // @ts-ignore
        const newHeaders = window.sign(realUrl, requestConfig.data) || {}
        // FontId
        const newHeadersFontId = window.f && typeof window.f === 'function' ? window.f() : {}

        requestConfig.headers = {
          ...requestConfig.headers,
          ...newHeaders,
          ...newHeadersFontId,
        }
      }

      return requestConfig
    })
    // @ts-ignore
    Launcher.http.interceptors.spam.use(response => {
      const kongHeader = response?.headers && response.headers['x-kong-sign']
      // response = 406TIME_ERROR_CODE = 2 406
      if (response.status === SPAM_CODE) { // 406
        if (kongHeader && kongHeader === TIME_ERROR_CODE) {
          alert('') // eslint-disable-line no-alert
          info('systemTimeError', undefined, 'system_time_error')
        } else if (spamNeedReload(kongHeader)) {
          window.location.reload()
        }
      } else if (kongHeader && spamNeedReload(kongHeader)) {
        window.location.reload()
      }
      return response
    })
    this.installed = true
  },
}
