import type { IGetDomTrackerData, ITrackerImpressionPayload } from '../types/helps/get-post-data'

const MAX_RETRY_LIMIT = 20
const __DEBUG__ = process.env.BUILD_ENV !== 'production' // eslint-disable-line

const getDomTrackerData = function (targetDom, noPropagation = false, retries = 0) {
  // targetDom.parentNode may be null
  if (!targetDom) {
    return null
  }

  // document.body should be the top
  // or document.body.parentNode.parentNode is #document
  // which has no method hasAttribute
  if (targetDom === document.body) {
    return null
  }

  if (retries > MAX_RETRY_LIMIT) {
    return null
  }
  if ((targetDom as Element).hasAttribute('data-eaglet') || (targetDom as Element).hasAttribute('eaglet')) {
    return targetDom
  }

  // when the target DOM has an eaglet-no-propagation attribute, DOM tracker data is not obtained.
  const isNoPropagation = (targetDom as Element).hasAttribute('eaglet-no-propagation')

  if (noPropagation || isNoPropagation) {
    return null
  }

  return getDomTrackerData(targetDom?.parentNode, false, ++retries)
} as IGetDomTrackerData

export function getPostData(targetDom: Node | Element | null, noPropagation: boolean = false, trackerType: string = 'click') {
  if (!targetDom) {
    throw new Error(`targetDom should not be ${targetDom}`)
  }
  let eagletDom: Node | Element | null = null; let eagletData: null | string | Record<string, unknown> = null
  if (trackerType === 'click') {
    // clickdom 
    eagletDom = getDomTrackerData(targetDom, noPropagation)
  } else if (trackerType === 'impression') {
    // 
    eagletDom = targetDom
  }

  if (!eagletDom) {
    return
  }
  // click
  if (trackerType === 'click') {
    eagletData = (eagletDom as HTMLElement).dataset.eaglet || (eagletDom as HTMLElement).getAttribute('eaglet')
  } else if (trackerType === 'impression') {
    eagletData = (eagletDom as HTMLElement).dataset.impression || (eagletDom as HTMLElement).dataset.eaglet || (eagletDom as HTMLElement).getAttribute('eaglet')
  }
  if (!eagletData) {
    return
  }
  const postData = JSON.parse(eagletData) as ITrackerImpressionPayload
  /**
   * (pointId > 2e4)
   *  data-impression
   * data-eaglet,data-impression
   *  data-eagletimpressionclickpointId
   * */
  const pointId = postData?.event?.value?.pointId
  const IsNewImpression = Number(pointId) >= 20000
  if (IsNewImpression && (eagletDom as HTMLElement).dataset.eaglet && !(eagletDom as HTMLElement).dataset.impression) {
    if (__DEBUG__) {
      throw new Error(`[eagletPlugin]  pointId >= 20000, data-eaglet[${postData?.event?.value?.pointId}]`)
    }
  }
  return postData
}
