// eslint-disable-next-line import/no-unresolved
import { SpvType, subscribe } from '@xhs/ozone-schema'
import Launcher from '@xhs/launcher'
// eslint-disable-next-line import/no-unresolved
import { commonChecks } from '@xhs/ozone-detector'
import { RouteLocationNormalizedLoaded } from 'vue-router'

import getPvInfo from '../data/pv'
import { appearListCases, spvType } from './flowSource'
import INIATORTYPE_MAP from '../data/meta/initiatorType'
import type {
  IEagletMethods,
  IApmJsonTrackerContent,
  ITrackerContent,
  IEagletOps,
  ITrackerExtendDataPayload,
} from '../types/plugin'

import type { IViewDidAppearBridgeCallBackRes } from '../types/collector/duration'
//  beforeunload 
const beforeunloadFlag = {
  value: false,
}

//  pv/pe 
let isPvPeMatched = true

const judgePlatform = () => {
  if (commonChecks.isIOS) {
    if (commonChecks.isXHS) {
      return 'iOS'
    }
    return 'iOSBrowser'
  }
  if (commonChecks.isAndroid) {
    if (commonChecks.isXHS) {
      return 'Android'
    }
    return 'AndroidBrowser'
  }
  return 'PC'
}
// H5, route.afterEach  pv,  viewDidAppear  pv
let lockFirstPV = 0

/**  pv  */
export const reportPv = (tracker: IEagletMethods, liteTracker: (() => Promise<unknown>) | undefined, route: RouteLocationNormalizedLoaded, trackersReady: Promise<(IApmJsonTrackerContent | ITrackerContent)[]>, initiatorType: number) => {
  //  pv 
  isPvPeMatched = false

  trackersReady.then(() => {
    if (typeof route?.meta?.getTrackerData === 'function') {
      const getTrackerDataRet = route.meta.getTrackerData(route)

      if (getTrackerDataRet instanceof Promise) {
        getTrackerDataRet.then(data => {
          tracker.push({
            ...data,
            page: {
              type: 'Page',
              value: {
                initiatorType,
                ...data?.page?.value,
              },
            },
          })
        })
      } else if (getTrackerDataRet) {
        tracker.push({
          ...getTrackerDataRet,
          page: {
            type: 'Page',
            value: {
              initiatorType,
              ...getTrackerDataRet?.page?.value,
            },
          },
        })
      }
    } else if (liteTracker) {
      // liteTracker  PV  getTrackerData 
      throw new Error('[eagletPlugin] `getTrackerData` in route.meta is necessary when use liteTracker!')
    } else {
      tracker.flush(getPvInfo(initiatorType))
    }
  })
}

class PageDurationCollector {
  tracker: IEagletMethods

  firstEnter: boolean

  pageStartTime: number

  trackPageView?: boolean

  blurAndVisibilityFlag: {
    pvReady?: boolean
    peReady?: boolean
  }

  route: RouteLocationNormalizedLoaded

  durationMsData: number

  appearListener: {
    remove: () => void
  }

  disappearListener: {
    remove: () => void
  }

  appearListenerIOS: {
    remove: () => void
  }

  disappearListenerIOS: {
    remove: () => void
  }

  constructor(app: Launcher, tracker: IEagletMethods, trackersReady: Promise<(IApmJsonTrackerContent | ITrackerContent)[]>, options: IEagletOps) {
    this.tracker = tracker
    this.firstEnter = true
    this.pageStartTime = Date.now()
    this.trackPageView = options.trackPageView
    //  pv\pe 
    this.blurAndVisibilityFlag = {
      pvReady: false,
      peReady: true,
    }
    // @ts-ignore reason: router ,
    this.route = {}

    this.durationMsData = 0

    this.appearListener = {
      remove: () => null,
    }
    this.disappearListener = {
      remove: () => null,
    }
    this.appearListenerIOS = {
      remove: () => null,
    }
    this.disappearListenerIOS = {
      remove: () => null,
    }

    const trackPageEnd = (route: RouteLocationNormalizedLoaded, initiatorType: number) => {
      //  pe  tracker 
      trackersReady.then(() => {
        if (isPvPeMatched) {
          return
        }
        lockFirstPV = -1
        this.durationMsData = this.durationMs()
        const currentRoute = route || this.route
        if (typeof currentRoute?.meta?.getPageEnd === 'function') {
          const getPageEndRet = currentRoute.meta.getPageEnd(this.route)
          trackersReady.then(() => {
            if (getPageEndRet instanceof Promise) {
              getPageEndRet.then(data => {
                const payload: ITrackerExtendDataPayload = {
                  ...data,
                  page: {
                    type: 'Page',
                    value: {
                      initiatorType,
                      durationMs: this.durationMsData,
                      ...data?.page?.value,
                    },
                  },
                }

                if (beforeunloadFlag.value) {
                  payload._uba_sendBeacon = true // eslint-disable-line
                  beforeunloadFlag.value = false
                }
                this.tracker.push(payload)
                isPvPeMatched = true
              })
            } else {
              const payload: ITrackerExtendDataPayload = {
                ...getPageEndRet,
                page: {
                  type: 'Page',
                  value: {
                    initiatorType,
                    durationMs: this.durationMsData,
                    ...getPageEndRet?.page?.value,
                  },
                },
              }
              // pe  sendBeacon 
              if (beforeunloadFlag.value) {
                payload._uba_sendBeacon = true  // eslint-disable-line
                beforeunloadFlag.value = false
              }
              this.tracker.push(payload)
              isPvPeMatched = true
            }
            // pe0beforeunloadpe
            this.pageStartTime = 0
          })
        }
      })
    }
    // pv
    const getTrackerData = (initiatorType: number) => {
      reportPv(this.tracker, undefined, this.route, trackersReady, initiatorType)
    }

    const handleViewDisappear :(initiatorType: number) => void = initiatorType => {
      const pageInstance = this.route?.meta?.pageInstance
      // ,PE
      if (!this.firstEnter && this.trackPageView && pageInstance) {
        trackPageEnd(this.route, initiatorType)
      }
    }

    // value:,initiatorType: pv
    const handleDidViewAppear:(value: IViewDidAppearBridgeCallBackRes, initiatorType: number) => void = (value, initiatorType) => {
      const index: number = value?.result || 1
      spvType.value = appearListCases[index] as SpvType
      this.firstEnter = false
      this.pageStartTime = Date.now()
      const pageInstance = this.route?.meta?.pageInstance
      if (this.trackPageView && pageInstance) {
        getTrackerData(initiatorType)
      }
    }
    const handleVisibleVisible = (initiatorType: number) => {
      this.pageStartTime = Date.now()
      const pageInstance = this.route?.meta?.pageInstance
      // pv
      if (this.trackPageView && pageInstance) {
        getTrackerData(initiatorType)
      }
    }
    const handleVisibleHidden = (initiatorType: number) => {
      const pageInstance = this.route?.meta?.pageInstance
      if (this.trackPageView && pageInstance) {
        trackPageEnd(this.route, initiatorType)
      }
    }
    const viewDidAppear = (value: IViewDidAppearBridgeCallBackRes) => {
      //  H5 ,  route.afterEach  pv,  viewDidAppear  pv;  pe  viewDidAppear , lockFirstPV ,  viewDidAppear  pv
      if (lockFirstPV === 0) {
        lockFirstPV = -1
        return
      }
      // param_1:, param_2:pv,README
      handleDidViewAppear(value, INIATORTYPE_MAP.PAGEVIEW.VIEWDIDAPPEAR)
      //
    }

    const viewDidDisAppear = (initiatorType: number) => {
      //  initiatorType undefined, viewDidDisappear 
      if (!initiatorType) {
        initiatorType = INIATORTYPE_MAP.PAGE_END.VIEWDIDDISAPPEAR
      }
      handleViewDisappear(initiatorType)
    }
    const viewDidDisAppearWrapper = () => {
      viewDidDisAppear(INIATORTYPE_MAP.PAGE_END.VIEWDIDDISAPPEAR)
    }

    // initiatorType:pv
    const visibilitychangeVisible = (initiatorType: number) => {
      handleVisibleVisible(initiatorType)
    }
    // initiatorType:pe
    const visibilitychangeHidden = (initiatorType: number) => {
      handleVisibleHidden(initiatorType)
    }

    // 
    if (commonChecks.isXHS) {
    // Android
      if (judgePlatform() === 'Android') {
        this.appearListener = subscribe('viewDidAppear', viewDidAppear)
        this.disappearListener = subscribe('viewDidDisappear', viewDidDisAppearWrapper)
      }
      // iOS
      if (judgePlatform() === 'iOS') {
        // this.appearListenerIOS = addEventListener('viewWillAppearIOS', viewDidAppear)
        // this.disappearListenerIOS = addEventListener('viewWillDisappearIOS', viewDidDisAppear)
        this.appearListenerIOS = subscribe('viewDidAppear', viewDidAppear)
        this.disappearListenerIOS = subscribe('viewDisappear', viewDidDisAppearWrapper)
      }
    } else { // 
      window.addEventListener('visibilitychange', () => {
        if (document.visibilityState === 'visible' && this.blurAndVisibilityFlag.pvReady) {
          this.updateBlurAndVisibilityFlag(false, true)
          visibilitychangeVisible(INIATORTYPE_MAP.PAGEVIEW.VISIBLE)
        } else if (document.visibilityState === 'hidden' && this.blurAndVisibilityFlag.peReady) {
          this.updateBlurAndVisibilityFlag(true, false)
          visibilitychangeHidden(INIATORTYPE_MAP.PAGE_END.HIDDEN)
        }
      })
      window.addEventListener('beforeunload', () => {
        //  hidden, hidden  pe 
        if (document.visibilityState === 'hidden') {
          return
        }
        //  beforeunloadFlag 
        beforeunloadFlag.value = true
        this.updateBlurAndVisibilityFlag(true, false)
        viewDidDisAppear(INIATORTYPE_MAP.PAGE_END.BEFOREUNLOAD)
      })
      window.addEventListener('focus', () => {
        if (this.blurAndVisibilityFlag.pvReady) {
          this.updateBlurAndVisibilityFlag(false, true)
          visibilitychangeVisible(INIATORTYPE_MAP.PAGEVIEW.FOCUS)
        }
      })
      window.addEventListener('blur', () => {
        if (this.blurAndVisibilityFlag.peReady) {
          this.updateBlurAndVisibilityFlag(true, false)
          visibilitychangeHidden(INIATORTYPE_MAP.PAGE_END.BLUR)
        }
      })
    }
    const handleBeforeRouteViewDisappear = (route: RouteLocationNormalizedLoaded, initiatorType: number) => {
      const pageInstance = this.route.meta?.pageInstance
      // ,PE
      if (this.firstEnter) {
        this.firstEnter = false
      } else if (this.trackPageView && pageInstance) {
        trackPageEnd(route, initiatorType)
      }
    }
    const handlerAfterRouteDidViewAppear = (route: RouteLocationNormalizedLoaded) => {
      this.route = route
      this.pageStartTime = Date.now()
    }
    //  PE 
    /* eslint-disable */
    app.router.beforeEach((to, from, next) => {
      handleBeforeRouteViewDisappear(from, INIATORTYPE_MAP.PAGE_END.BEFOREEACH)
      if (typeof next === 'function') next()
    })
    /* eslint-disable */
    app.router.afterEach((to, from) => {  
      handlerAfterRouteDidViewAppear(to)
    })
  }

  durationMs() {
    //  pe  beforeunload 
    if(this.pageStartTime === 0 ){
      return 0
    }
    return Date.now() - this.pageStartTime
  }
  // blurvisibilitychange  pvpe 
  updateBlurAndVisibilityFlag(isPvReady: boolean, isPeReady: boolean){
    this.blurAndVisibilityFlag.pvReady = isPvReady
    this.blurAndVisibilityFlag.peReady = isPeReady
  }
}

export default function createPageDurationCollector(app: Launcher, tracker: IEagletMethods, trackersReady: Promise<(IApmJsonTrackerContent | ITrackerContent)[]>, options: IEagletOps) {
  return new PageDurationCollector(app, tracker, trackersReady, options)
}