import Launcher from '@xhs/launcher'
import { IApmJsError, ICustomErrorOptions } from '../../index.d'
import type { IEagletMethods, ILauncher, Launcher2 } from '../types/plugin'

import type {
  HTTPError,
  IVueError,
  IJsErrorOptions,
  IOptions,
  IErrorTracker,
  ICustomPromiseRejectionEvent,
} from '../types/collector/jsError'

function isHTTPError(s: string): s is HTTPError {
  return ['HTTPClientError', 'HTTPServerError', 'HTTPBizError'].includes(s)
}

function ignoreDefaultError(err: IApmJsError) {
  if (err.errorType && isHTTPError(err.errorType)) return true
  if (err.errorType === 'AbortError' && err.errorMessage === 'AbortError') return true
  return false
}

/**
 *  ignoreErrors 
 */
function isIgnoreErrors(data: IApmJsError, rules: string[] = []) {
  const rule = `${data?.errorType}: ${data?.errorMessage}`
  return rules.includes(rule)
}

function getErrorOptions(options: IOptions) {
  const { level, title, extra = null } = options || {}

  const errorExtra = JSON.stringify({
    title,
    ...(typeof extra === 'object') && extra,
  })

  return {
    errorLevel: level,
    errorExtra,
  }
}

/**
 *  http 
 */
function catchHttpNetworkError(reason: {
  config: {
    method: string
    url: string
    data: unknown
    params: unknown
  }
  message: unknown
}) {
  const {
    method, url, data, params,
  } = reason.config || {
    method: '', url: '', data: '', params: '',
  }
  const metrics = {
    measurement_name: 'httpRequestTiming',
    measurement_data: {
      method,
      errorType: 'HTTPNetworkError',
      url,
      // 
      errorMsg: reason.message,
      data: method === 'get' ? params : data,
    },
  }
  return metrics
}

export function parseError(err: Error | string, options?: ICustomErrorOptions) {
  if (err instanceof Error) {
    const errorMessage = err.message
    // reason:  TS 
    let errorFilePath = ''
    let errorLineNumber = 0
    let errorColNumber = 0
    const errorType = err.name
    let errorStack = ''
    if (err.stack) {
      const lines = err.stack?.split('\n')
      for (let i = 0; i < lines?.length; i++) {
        const matched = /:(\d+:\d+)\)$/.exec(lines[i])
        if (matched?.[1]) {
          const errorPosition = matched[1].split(':')
          errorFilePath = lines[i]
          errorLineNumber = parseInt(errorPosition[0], 10) || 0
          errorColNumber = parseInt(errorPosition[1], 10) || 0
          break
        }
      }
    }
    if (err.stack) {
      errorStack = err.stack
    }

    return {
      errorLineNumber,
      errorColNumber,
      errorFilePath: errorFilePath?.trim(),
      errorStack,
      errorType,
      errorMessage,
      ...options && getErrorOptions(options),
    }
  } if (typeof err === 'string') {
    return {
      errorType: 'Error',
      errorMessage: err,
      ...options && getErrorOptions(options),
    }
  }

  return null
}

export default (app: Launcher, Launcher: ILauncher, eaglet: IEagletMethods, options: IJsErrorOptions | true) => {
  const jsErrorOptions = {
    enableIgnoreDefaultError: true,
    ignoreErrors: [],
    ignoreError: null,
    ...(typeof options === 'object') && options,
  }
  const {
    ignoreErrors, ignoreError, enableIgnoreDefaultError, microApp,
  } = jsErrorOptions

  function sendInfraJsError(payload: IApmJsError) {
    if (isIgnoreErrors(payload, ignoreErrors)) return
    if (enableIgnoreDefaultError === true && ignoreDefaultError(payload)) return
    if (ignoreError?.(payload)) return

    const metrics = {
      measurement_name: 'infraJsError',
      measurement_data: payload,
    }

    window.apm_blankScreen_error = {
      errorType: 'JSError',
      content: payload.errorMessage,
    }

    eaglet.push(metrics, 'ApmXrayTracker').catch((err: unknown) => {
      /**
       * : eaglet.push 
       */
      if (process.env.BUILD_ENV !== 'production') {
        console.error(err)
      }
    })
  }

  // eslint-disable-next-line
  const errorTrackerBuffurs = window.__ERROR_TRACKER_BUFFER__ || []
  while (errorTrackerBuffurs.length) {
    const { error, extra } = errorTrackerBuffurs.shift() as IErrorTracker
    const { colno, lineno, source } = extra
    const payload: IApmJsError = {
      type: 'onerror',
      errorLineNumber: lineno,
      errorColNumber: colno,
      errorFilePath: source?.trim(),
      errorStack: error?.stack,
      errorType: error?.name,
      // error  message
      errorMessage: String(error?.message || error),
    }

    sendInfraJsError(payload)
  }

  function sendVueError(err: IVueError) {
    // Vue 
    const result = parseError(err)

    if (result) {
      const payload: IApmJsError = {
        type: 'vueError',
        ...result,
      }
      sendInfraJsError(payload)
    }
  }

  // Vue 
  if (Launcher.version?.startsWith('3') && app?.vm?.config?.errorHandler) {
    app.vm.config.errorHandler = (err: unknown) => {
      sendVueError(err as Error)
    }
  // @ts-ignore reason:  vue2 ,  Launcher2 , 
  } else if ((Launcher as unknown as Launcher2)?.Vue?.config?.errorHandler) {
    // @ts-ignore reason:  vue2 ,  Launcher2 , 
    (Launcher as Launcher2).Vue.config.errorHandler = (err: unknown) => {
      sendVueError(err as Error)
    }
  }

  //  unhandledrejection qiankun onerror 
  if (microApp) {
    return
  }

  // JS 
  window.onerror = (message, source, lineno, colno, error) => {
    const payload: IApmJsError = {
      type: 'onerror',
      errorLineNumber: lineno,
      errorColNumber: colno,
      errorFilePath: source?.trim(),
      errorStack: error?.stack,
      errorType: error?.name,
      errorMessage: String(message),
    }
    sendInfraJsError(payload)
  }

  // Promise 
  window.addEventListener('unhandledrejection', (event: ICustomPromiseRejectionEvent) => {
    // reason:  TS 
    let errorMessage = ''
    let errorFilePath = ''
    let errorLineNumber = 0
    let errorColNumber = 0
    const errorType = event?.reason?.name
    let errorStack = ''

    if (typeof event.reason === 'string') {
      errorMessage = event.reason
    } else if (typeof event.reason === 'object') {
      //  http 
      if (event.reason.isAxiosError) {
        const payload = catchHttpNetworkError(event.reason)
        eaglet.push(payload, 'ApmXrayTracker')
        return
      }
      if (event.reason.stack) {
        const lines = event.reason.stack?.split('\n') as string[]
        for (let i = 0; i < lines?.length; i++) {
          const matched = /:(\d+:\d+)\)$/.exec(lines[i])
          if (matched?.[1]) {
            const errorPosition = matched[1].split(':')
            errorFilePath = lines[i]
            errorLineNumber = parseInt(errorPosition[0], 10) || 0
            errorColNumber = parseInt(errorPosition[1], 10) || 0
            break
          }
        }
      }
      errorMessage = event.reason.message
      errorStack = event.reason.stack
    }

    const payload = {
      type: 'promise',
      errorLineNumber,
      errorColNumber,
      errorFilePath: errorFilePath?.trim(),
      errorStack,
      errorType,
      errorMessage,
    }
    sendInfraJsError(payload)
  })
}
