// do not change the order!
export const BROWSER_UA_SPECS = [
  'qq', // mobile QQ
  'mqqbrowser', // mobile QQBrowser
  'ucbrowser', // UCBrowser
  'baiduboxapp', // 
  'baiduspider', // Baidu spider
  'baidubrowser', // Baidu browser
  'micromessenger', // Weixin
  'weibo', // 
  'alipay', // 
  'discover', // XHSWebView 6.18+
  'redtop', // WebView
  'uniik', // uniik app
  'spark', // spark app
  'merchant', //  app
  'snowpeak', // takib app
  'internation', // internation app
  'catalog', // catalog app
  'odyssey', // spark app
]

export function getBrowser(UA = '') {
  let browser = 'unspecified'
  let ua

  if (!process.env.BROWSER) {
    ua = UA.toLowerCase()
  } else {
    ua = (UA || window.navigator.userAgent).toLowerCase()
  }

  /*
   * :
   * 1. 
   * 2. 
   *
   */

  browser = BROWSER_UA_SPECS.reduce((ret, name) => {
    if (ua.indexOf(name) >= 0) {
      ret = name // eslint-disable-line no-param-reassign
    }
    return ret
  }, browser)

  /*
   * 
   * iOSmac safari
   * WindowAndroid chrome
   */

  if (browser === 'unspecified') {
    if (ua.indexOf('iphone') >= 0 || ua.indexOf('macintosh') >= 0) {
      browser = 'safari'
    } else if (ua.indexOf('android') >= 0 || ua.indexOf('windows nt') >= 0) {
      browser = 'chrome'
    }
  }

  //  Android QQ ua MQQBrowser QQQQ
  //  ios QQua QQMQQBrowser,QQ ua  MQQBrowser  QQ

  if (browser === 'mqqbrowser') {
    // qq android MQQBrowser QQ 
    if (ua.indexOf('android') >= 0
      && /mqqbrowser/i.test(ua) && /qq/i.test(ua.replace('mqqbrowser', ''))) {
      browser = 'qq'
    }
  }

  return browser
}

export function getBrowserVersion(UA = '') {
  const version = {
    major: 0,
    minor: 0,
    patch: 0,
  }

  let ua

  if (!process.env.BROWSER) {
    ua = UA.toLowerCase()
  } else {
    ua = (UA || window.navigator.userAgent).toLowerCase()
  }

  /**
   * `XHS` for old XHS WebView
   * `Version` for new XHS WebView & Safari
   * `discover` for new XHSWebView: 6.18
   * `Chrome` for Chrome
   */
  let regex

  if (ua.includes('discover')) {
    regex = /(?:discover)\/(\d+)\.(\d+)\.?(\d+)?/
  } else {
    regex = /(?:XHS|Version|Chrome)\/(\d+)\.(\d+)\.?(\d+)?/
  }
  const result = regex.exec(ua)

  if (result) {
    version.major = parseInt(result[1], 10)
    version.minor = parseInt(result[2], 10)

    if (result[3] !== undefined) {
      version.patch = parseInt(result[3], 10)
    }
  }

  return version
}
