import { BLOCKED_HOSTS } from '../consts'

export function now() {
  return Date.now() || +new Date()
}

/**
 * 
 * @param {string} requestUrl 
 */
export function isForbiddenUrl(requestUrl = '', urls = BLOCKED_HOSTS) {
  return urls.some(url => requestUrl.indexOf(url) !== -1)
}

/**
 *  mark 
 * @param {array} marks mark 
 * @param {string} name 
 */
export function getTimeFromMarks(marks, name) {
  const [matchingMark] = marks.filter(mark => mark.name === name)

  return matchingMark ? matchingMark.startTime : null
}

/**
 *  Performance  json 
 * @param {object} target 
 */
export function toDict(target = {}) {
  if (target.toJSON && typeof target.toJSON === 'function') {
    return target.toJSON()
  }

  return Object.keys(Object.getPrototypeOf(target)).reduce((result, key) => {
    if (typeof target[key] !== 'function') {
      result[key] = target[key]
    }
    return result
  }, {})
}
