/**
 * From uuid [3.x.x](https://github.com/uuidjs/uuid/blob/3df73a98f07c0a38a94bcaf1ecde0e384dc3b126/lib/rng-browser.js#L26)
 *
 * ****
 *  uuid 8.x.x  rn `getRandomValues() not supported` 
 *
 * ****
 *  react-native  [react-native-get-random-values](https://github.com/uuidjs/uuid#getrandomvalues-not-supported) 
 */
const rnds = new Array(16);
export default function makeRng() {
    for (let i = 0, r; i < 16; i++) {
        if ((i & 0x03) === 0) { // eslint-disable-line
            r = Math.random() * 0x100000000;
        }
        rnds[i] = r >>> ((i & 0x03) << 3) & 0xff; // eslint-disable-line
    }
    return rnds;
}
