import { commonChecks } from '@xhs/ozone-detector'

/**
 * 
 * @param success true false 
 * @constructor
 */
const DefaultHandlePostLogin = success => {
  if (success) {
    // 
    window.location.reload()
    return
  }
  // 
  window.close()
}

/**
 * 
 * @param callback
 * @param checkLoginWithAction
 */
function handlePostLogin(callback, checkLoginWithAction) {
  // 
  if (!commonChecks.isXHS) {
    return
  }
  const afterLogin = typeof callback === 'function' ? callback : DefaultHandlePostLogin
  /**
   * type  0 
   *  @xhs/ozone-bridge checkLoginWithAction
   */
  checkLoginWithAction({ type: 0 }).then(({ isLogin, loginChange }) => {
    // 
    if (!isLogin && !loginChange) {
      afterLogin(false)
      return
    }
    // 
    if (isLogin && loginChange) {
      afterLogin(true)
    }
  })
}

export default handlePostLogin
