// window.insight   disableApm = true   eaglet apm 
export const isUpgrade = (disableApm: Boolean) => {
  if (window.insight && !disableApm) {
    throw new Error(' launcher-plugin-eaglet  apm  false')
  }
  return !!window.insight
}

// Array.include 
const APM_TRACKER_NAMES = [APM_JSON_TRACKER.NAME, APM_XRAY_TRACKER.NAME]
type apmTrackerNamesType = typeof APM_TRACKER_NAMES
type apmTrackerNamesItemType = apmTrackerNamesType[number]

export const isApmTracker = (name: ITrackerName) => APM_TRACKER_NAMES.includes(name as apmTrackerNamesItemType)
