/* eslint-disable */
import { getFlagValue } from '@xhs/abtest'
import { commonChecks } from '@xhs/ozone-detector'
import  Launcher from '@xhs/launcher'

import createClickCollector from './collector/click'
import createImpressionCollector from './collector/impression'
import createPerformanceCollector from './collector/performance'
import createPageDurationCollector, { reportPv } from './collector/duration'
import createJsErrorCollector, { parseError } from './collector/jsError'
import { checkBlankScreenError } from './collector/blank-screen'
import createTracker from './helpers/create-tracker'
import createSendBeaconTracker from './helpers/sendBeacon/createSendBeaconTracker'
import createPerfUtils from './helpers/create-perf-utils'
import trackerDataMerge from './helpers/trackerDataMerge'
import initiatorTypeProcess from './helpers/initiatorTypeProcess'
import { addEventSeqIdInSession } from './helpers/addEventSeqIdInSession'
import { addBrowserSource, updateBrowserSource } from './helpers/channelSource'
import setUserId from './helpers/setUserId'

import getMetaInfo, { getBaseInfo } from './data/meta'
import addEventInfo from './data/event'
import getPvInfo from './data/pv'
import INIATORTYPE_MAP from './data/meta/initiatorType'

// 
import interceptFactory from './interceptor'

import { flowSourcePointProcess, flowSourceConfig } from './collector/flowSource'
import {
  DEFAULT_TRACKER, APM_TRACKER, APM_JSON_TRACKER, APM_XRAY_TRACKER,
} from './const'
import checkTrackerInstalled from './libs/checkTrackerInstalled'
import pbToPave, { needPbToPave, } from './helpers/pbToPave'


import { isUpgrade, isApmTracker } from './helpers/base'
// import pack from '../package.json'
import getPageInfo from './data/meta/page'

import type { RouteLocationNormalizedLoaded } from 'vue-router'

import type {
  ILauncher,
  IEagletOps,
  IEagletMethods,
  IEaglet,
  IProcessQueue,
  ITrackerMap,
  IExtendData,
  ITrackerContent,
  IPushPayload,
  IUniversalData,
  IPurgeBaseInfoConfig,
  ITrackerPayload,
  ITrackerName,
  IConfigTrackerName,
  IPayload,
  IFlushApmPayload,
  ITrackerExtendDataPayload,
  IPushFunction,
  IConfigFunction,
  INewExtendFunction,
  ITrackers,
  IApmJsonTrackerContent,
  IApmXrayTrackerExtendDataPayload,
  IConfigName,
  IApmJSONTrackerPayload,
  IExtendPayload,
  ApmXrayTrackerName,
  ILitePushPayload,
  Launcher2,
  LaunhcerWithAuth,
} from './types/plugin'
import type { INeedPbToPaveData, IFrontPb, IXrayData } from './types/helps/pbToPave'
import { Tracker } from './interceptor/index'



const processQueue: IProcessQueue[] = []
// eslint-disable-next-line
//  ts-ignore eagletTrack  pushflushconfigextendgetABFlagValue ts-ignore 
//@ts-ignore
let eagletTrack = {
  push: async (...args) => {
    processQueue.push({ type: 'push', params: args })
  },
  flush: async (...args) => {
    processQueue.push({ type: 'flush', params: args })
  },
  config: (...args) => {
    processQueue.push({ type: 'config', params: args })
  },
  extend: (...args) => {
    processQueue.push({ type: 'extend', params: args })
  },
  getABFlagValue(...args) {
    processQueue.push({ type: 'getABFlagValue', params: args })
  },
} as IEagletMethods

let isFirstEnter = true


const trackerMap: ITrackerMap = {}
const extendData: IExtendData = {}

const __DEBUG__ = process.env.BUILD_ENV !== 'production' // eslint-disable-line


const Eaglet: IEaglet = {
  // version: pack.version,

  name: 'eaglet',

  installed: false,

  trackPageView: true,

  trackPageEnd: true,


  sendBeaconTracker: {
    push: () => {}
  },

  //@ts-ignore  reason: trackerBuilder , install  trackerBuilder 
  trackerBuilder: {},

  trackers: {},


  install(app: Launcher, Launcher: ILauncher, options: IEagletOps = {}) {
    if (this.installed) {
      return
    }
    if (!Launcher.isBrowser) {
      return
    }
    if (!app.router) {
      return
    }
    const apmJsonTracker = {
      NAME: 'ApmTracker',
      PROTOBUF_HASH: 1545015918,
      dataType: 'json',
      VERSION: '0.1.33',
      builder: {
        build: (trackerName: string, payload: unknown) => payload,
      },
      jspb: {},
    }
    const {
      tracker,
      liteTracker,
      apmTracker,
      trackPageView,
      trackPageEnd,
      enableNativeEmitter = true,
      performance,
      // apm: false  apm 
      apm = {},
      // tslint:enable
      referPath = false,
      customEndPoint,
      noSameRouter,
    } = options
    const {
      jsError = false,
      http: {
        enableBatchRequest = true,
        enableResponseData = false,
      } = {},
      interceptor,
      blankScreenError: {
        threshold = 0.7, //  70% 
        domThreshold = 5, //  dom 
        maxDepth = 10, // dom 
        lines = 5, // 
        columns = 10, // 
        autoCheckBS = false, // autoCheckTime
        autoCheckBSTime = 3000,
        screenshot = true, // 
        rootElementSelector = '', //  dom 
      } = {},
    } = apm as Exclude<IEagletOps['apm'], boolean | undefined>


    // apm = false  apm 
    const disableApm = options.apm === false

    const blankScreenErrorConfig = {
      threshold, domThreshold, maxDepth, lines, columns, autoCheckBS, autoCheckBSTime, screenshot, rootElementSelector
    }

    this.installed = true

    if (tracker) {
      this.trackers.tracker = tracker
      tracker().then((res: ITrackerContent) => {
        this.trackerBuilder = res.builder
      })
    }

    if (liteTracker) {
      if (tracker) {
        // reason: Error class 
        throw new Error('[eagletPlugin] Cannot both use tracker and liteTracker')
      }

      this.trackers.liteTracker = liteTracker
    }

    if (apmTracker) {
      this.trackers.apmTracker = apmTracker
    } else {
      // this.trackers.apmJsonTracker = apmJsonTracker
      this.trackers.apmTracker = apmJsonTracker
    }

    if (typeof trackPageView !== 'undefined') {
      this.trackPageView = trackPageView
    }

    if (typeof trackPageEnd !== 'undefined') {
      this.trackPageEnd = trackPageEnd
    }

    const trackersReady = Promise.all(Object.keys(this.trackers).map((trackerKey: string) => {
      if (typeof this.trackers[trackerKey as keyof ITrackers] === 'function') {
        return (this.trackers[trackerKey as keyof ITrackers] as () => Promise<ITrackerContent | IApmJsonTrackerContent>)()
      }
      return this.trackers[trackerKey as 'apmTracker'] as IApmJsonTrackerContent
    })).then(trackers => trackers.map((tracker, idx) => {
      const trackerType = Object.keys(this.trackers)[idx]

      if (tracker.NAME === DEFAULT_TRACKER.NAME) {
        if (trackerType === 'liteTracker' && !tracker.IS_LITE) {
          throw new Error('[eagletPlugin] Illegal liteTracker')
        }
        if (trackerType === 'tracker' && tracker.IS_LITE) {
          throw new Error('[eagletPlugin] Illegal tracker')
        }
      } else if (trackerType !== 'apmTracker') {
        throw new Error(`[eagletPlugin] Illegal ${trackerType}`)
      }

      return tracker
    })).then(trackers => {
      trackers.forEach(tracker => {
        //  tracker  pageview  pageend enum  pv  pe
        if (tracker.NAME === DEFAULT_TRACKER.NAME && !tracker.IS_LITE) {
          const action = (tracker.builder as ITrackerContent['builder']).NormalizedAction

          if (this.trackPageView) {
            this.trackPageView = typeof action?.PAGEVIEW !== 'undefined'
          }

          if (this.trackPageEnd) {
            this.trackPageEnd = typeof action?.PAGE_END !== 'undefined'
          }
        }

        if (__DEBUG__) {
          tracker.debug = typeof options.debug === 'undefined' ? true : options.debug
        }

        if (!__DEBUG__) {
          tracker.debug = window.location.href.includes('eaglet_debug')
        }
        trackerMap[tracker.NAME] = createTracker(tracker, {
          enableNativeEmitter,
          enableBatchRequest: performance?.enableBatchRequest ?? enableBatchRequest,
          customEndPoint
        })
        //  sendBeacon tracker

        if (tracker.NAME === DEFAULT_TRACKER.NAME && !tracker.IS_LITE && !commonChecks.isXHS) {
          this.sendBeaconTracker = createSendBeaconTracker(tracker, customEndPoint)
        }
      })
      return trackers
    })

    const getTrackerName = (trackerName?: ITrackerName): Promise<ITrackerName> => trackersReady.then(
      trackers => {
        return trackerName || trackers[0].NAME as ITrackerName
      },
    )

    const purgeMetaInfoReady = new Promise<void>(resolve => {
      const purgeMetaInfo: (config: IPurgeBaseInfoConfig) => Promise<void> = (config: IPurgeBaseInfoConfig) => getMetaInfo(config, options).then(metaInfo => {
        eagletTrack.extend(metaInfo as IExtendPayload, DEFAULT_TRACKER.NAME)
        eagletTrack.extend(metaInfo as IExtendPayload, APM_JSON_TRACKER.NAME)
        eagletTrack.extend(metaInfo as IExtendPayload, APM_TRACKER.NAME)
        resolve()
      })

      app.router.afterEach((route: RouteLocationNormalizedLoaded, to: RouteLocationNormalizedLoaded) => {
        if (noSameRouter === true && route.fullPath == to.fullPath) {
          return
        }
       
        //  meta page  spa  page 
        eagletTrack.extend(getPageInfo(route), DEFAULT_TRACKER.NAME)

        const pageChange = () => {
          purgeMetaInfo({ route })

          const { pageInstance } = route.meta
          //  browserSource 
          updateBrowserSource(route)
          //  pv
          if (this.trackPageView && pageInstance) {
            reportPv(eagletTrack, liteTracker, route, trackersReady, INIATORTYPE_MAP.PAGEVIEW.AFTEREACH)
          }

          if (isFirstEnter) {
            createClickCollector(eagletTrack)
            createImpressionCollector(eagletTrack)
          }

          isFirstEnter = false
        }

        // tracker  pv
        trackersReady.then(pageChange)
      })

      //  
      if ((app as LaunhcerWithAuth).auth?.services) {
         // @ts-ignore reason: ,  as if  true
        if ((app as LaunhcerWithAuth).auth.services.registerAfterLoginCallback) {
          (app as LaunhcerWithAuth).auth.services.registerAfterLoginCallback(() => {
            purgeMetaInfo({ purgeUser: true })
          // @ts-ignore reason: true, 
          }, true)
        }
      }
    })

    const pushData = (payload: IPushPayload, trackerName: ITrackerName, options?: IEagletOps) => {
      return purgeMetaInfoReady.then(async () => {
        //  template format
        let data: IPushPayload

        //  sendBeacon 
        const sendBeaconFlag = {
          value: false,
        }
        if ((payload as ITrackerPayload)._uba_sendBeacon) {  // eslint-disable-line
          sendBeaconFlag.value = true
          delete (payload as ITrackerPayload)._uba_sendBeacon  // eslint-disable-line
        }
        //  liteTracker
        if (liteTracker && trackerName === DEFAULT_TRACKER.NAME) {
          if ((payload as ITrackerExtendDataPayload).templateData) {
            // extendData
            data = {
              ...(payload as ITrackerExtendDataPayload).templateData,
              ...extendData[trackerName as keyof IExtendData] as object,
            }
          } else { //  liteTracker  lite template
            throw new Error('[eagletPlugin] Illegal push payload, you use `liteTracker` in eaglet, make sure push data with lite template')
          }
        } else {
          if ((payload as ITrackerExtendDataPayload).templateData || (payload as ITrackerExtendDataPayload).templateBuffers) {
            throw new Error('[eagletPlugin] Illegal push payload, you use `Tracker` in eaglet, make sure push data with normal template')
          }
          data = {
            ...extendData[trackerName as keyof IExtendData] as object,
            ...payload,
            page: {
              type: ((payload as ITrackerPayload).page?.type || (extendData[trackerName as keyof IExtendData] as ITrackerPayload).page?.type),
              value: {
                ...(extendData[trackerName as keyof IExtendData] as ITrackerPayload)?.page?.value,
                ...(payload as ITrackerPayload).page?.value
              },
            },
          }

          // 
          if (trackerName === DEFAULT_TRACKER.NAME) {
            data = trackerDataMerge(this.trackerBuilder, data as ITrackerExtendDataPayload, extendData[trackerName as keyof IExtendData] as ITrackerExtendDataPayload, payload)
          }
        }
        data = addEventInfo(data as IPayload, trackerName, this.trackerBuilder)

        //  coldStartIdseqIdeventSeqIdInSession 
        if (trackerName === DEFAULT_TRACKER.NAME) {
          addEventSeqIdInSession(data as ITrackerExtendDataPayload, this.trackerBuilder)
        }


        // 
        if (referPath && trackerName === DEFAULT_TRACKER.NAME) {

          data = await flowSourcePointProcess(data as ITrackerExtendDataPayload, this.trackerBuilder)
        }

        // pvpe  page  initiatorType 
        if (trackerName === DEFAULT_TRACKER.NAME) {
          initiatorTypeProcess(data, this.trackerBuilder)
          //
          addBrowserSource(data, this.trackerBuilder)
        }

        // 
        if (trackerName !== DEFAULT_TRACKER.NAME) {
          //  coldStartId 

          delete (data as ITrackerExtendDataPayload)?.app?.value?.coldStartId
        }
        /**
         *  sdk  eaglet.push(..., 'ApmTracker')
         * 
         *  Apm https://doc.weixin.qq.com/doc/w3_AGUAoQYrACUbtBxHzwlSF6rfvDTqC?scode=ANAAyQcbAAg1twID0mAGUAoQYrACU
         */

        if (trackerName === 'ApmTracker') {
          if (needPbToPave(data as INeedPbToPaveData)) {
            const paveData = pbToPave(data as IFrontPb)
            paveData?.forEach(tracker => {

              //  insight sdk 
              if (isUpgrade(disableApm)) {
                // tracker meta   insight.push meta  insight 
                const { measurement_name, measurement_data } = tracker
                const filterValue = {
                  measurement_name,
                  measurement_data
                }
                window.insight?.push(filterValue as Tracker)
                return
              }
              trackerMap?.ApmTracker?.push(tracker as IPayload)
            })
          //  apm  data  browserApmTracker  frontApmTracker  apmLog  apm   
          //  apm 
          } else if (!!data.browserApmTracker || !!data.frontApmTracker || !!data.apmLog) {
            trackerMap?.ApmTracker?.push(data)
          }
          return
        }
        //  sendBeacon  pe 
        if (sendBeaconFlag.value) {
          sendBeaconFlag.value = false
          this.sendBeaconTracker.push(data)
          return
        }

        trackerMap[trackerName]?.push(liteTracker && trackerName === DEFAULT_TRACKER.NAME ? {
          data,
          templateBuffers: JSON.parse(JSON.stringify((payload as ITrackerExtendDataPayload).templateBuffers)),
        } as ILitePushPayload : data)
      })
    }

    const getBaseInfoReady: Promise<void> = new Promise(resolve => {
      const purgeBaseInfo: (config: IPurgeBaseInfoConfig) => void = config => getBaseInfo(config, options).then(baseInfo => {
        extendData[APM_XRAY_TRACKER.NAME as keyof IExtendData] = baseInfo

        resolve()
      })

      app.router.afterEach((route: RouteLocationNormalizedLoaded) => {
        getTrackerName().then(() => purgeBaseInfo({ route }))
      })

      //  
      if ((app as LaunhcerWithAuth).auth?.services) {
        // @ts-ignore reason: ,  as if  true
        if ((app as LaunhcerWithAuth).auth.services.registerAfterLoginCallback) {
          (app as LaunhcerWithAuth).auth.services?.registerAfterLoginCallback(() => {
            purgeBaseInfo({ purgeUser: true })
          // @ts-ignore reason: true, 
          }, true)
        }
      }
    })

    const intercept = interceptFactory(interceptor)

    const pushXrayData = (payload: IXrayData) => getBaseInfoReady.then(() => {

      const data = intercept({
        // 
        //@ts-ignore reason: clientTime 
        clientTime: Date.now(),
        ...extendData[APM_XRAY_TRACKER.NAME as ApmXrayTrackerName],
        ...payload,
      } as Tracker)

      //  insight sdk 
      if (isUpgrade(disableApm)) {
        window.insight?.push(data)
        return
      }

      trackerMap?.ApmTracker?.push(data)
    })

    eagletTrack = {
      supportResourceError: true,
      push: async (originPayload: IPushPayload, originTrackerName?: ITrackerName) => {
        let payload = originPayload
        let trackerName = originTrackerName
        if (!payload) {
          if (process.env.BUILD_ENV !== 'production') {
            console.error(`[eagletPlugin] The payload should not be empty`) // eslint-disable-line
          }
          return
        }
        if (trackerName === APM_XRAY_TRACKER.NAME) {
          /**
           * apm  apm  pb 
           */
          pushXrayData(payload as IXrayData)
          return
        }

        if (trackerName === 'ApmJSONTracker') {
          const messageName = (originPayload as IApmJSONTrackerPayload).type
          const metricValue: {
            [props: string]: string | number | object
          } = {}
          metricValue[messageName] = originPayload
          payload = {
            frontApmTracker: {
              type: 'FrontApmTracker',
              value: metricValue,
            },
          }
          trackerName = 'ApmTracker'
        }
        const finalName = await getTrackerName(trackerName)
        if (payload) {
          if (!checkTrackerInstalled(trackerMap, finalName)) {
            return
          }
        }
        pushData(payload, finalName, options)
      },

      flushApm: async (payload: IFlushApmPayload, trackerName: ApmXrayTrackerName) => {
        if (trackerName === 'ApmXrayTracker') {
          if (isUpgrade(disableApm)) {
            window.insight?.flush(payload)
            return
          }

          getBaseInfoReady.then(() => {
            const apmPayload: IFlushApmPayload = {
              // 
              clientTime: Date.now(),
              ...extendData[APM_XRAY_TRACKER.NAME],
              ...payload,
            }
            if (typeof trackerMap.ApmTracker?.flushApm === 'function') {
              trackerMap.ApmTracker?.flushApm(apmPayload)
            }
          })
        }
      },

      flush: async (payload: IPayload, trackerName?: ITrackerName ) => {
        const finalName = await getTrackerName(trackerName)
        if (!checkTrackerInstalled(trackerMap, finalName)) {
          return
        }

        if (isUpgrade(disableApm) && isApmTracker(finalName)) {
          window.insight?.flush(payload as IApmXrayTrackerExtendDataPayload)
          return
        }
        if (payload) {
          await eagletTrack.push(payload, finalName)
        }
        trackerMap[finalName]?.flush()
      },

      config: (name: IConfigName | string, payload: IUniversalData, trackerName: IConfigTrackerName) => {
        const finalName = trackerName || DEFAULT_TRACKER.NAME
        const dupPayload = JSON.parse(JSON.stringify(payload)) as IUniversalData

        if (finalName !== DEFAULT_TRACKER.NAME) {
          if (name === 'page') {
            delete dupPayload.value.pageInstance
          }
          if (name === 'browser') {
            delete dupPayload.value.fixedReferer
          }
        }
        if (finalName === 'ApmTracker') {
          delete dupPayload.value?.gid
        }
        if (finalName === 'ApmTracker' && name === 'app') {
          delete dupPayload.value?.buildId
        }
        if (extendData[finalName]) {
          // extendData[finalName][name] = dupPayload
          (extendData[finalName] as ITrackerExtendDataPayload)[name] = dupPayload
        } else {
          extendData[finalName] = {
            [name]: dupPayload,
          }
        }

        //  meta  apm-insight  apm   
        //  apm 
        if (isUpgrade(disableApm) && isApmTracker(finalName)) {
          const meta = JSON.parse(JSON.stringify(payload)) as IUniversalData
          if (name === 'app') {
            const { type, value: { platform, appVersion, artifactName, artifactVersion } } = meta
            window.insight?.config(name, { type, value: { platform, appVersion, artifactName, artifactVersion } }, trackerName)
          }
          if (name === 'mob') {
            const { type, value: { osVersion, dvceModel } } = meta
            window.insight?.config(name, { type, value: { osVersion, dvceModel } }, trackerName)
          }
          if (name === 'dvc') {
            const { type, value: { dvceId } } = meta
            window.insight?.config(name, { type, value: { dvceId } }, trackerName)
          }
          if (name === 'network') {
            window.insight?.config(name, meta, trackerName)
          }
          if (name === 'browser') {
            const { type, value: { matchedPath = [], route = '', userAgent = '' } = {} } = meta || {}
            window.insight?.config(name, { type, value: { matchedPath, route, userAgent } }, trackerName)
          }
          if (name === 'user') {
            const { type, value: { userId } } = meta
            window.insight?.config(name, { type, value: { userId } }, trackerName)
          }
        }
      },

      extend: (payload: IExtendPayload, trackerName: IConfigTrackerName) => {
        Object.keys(payload).forEach(key => {
          eagletTrack.config(key, (payload[key as keyof IExtendPayload] as IUniversalData), trackerName)
        })
      },

      getABFlagValue(flagName: string | string[]) {
        return getFlagValue(flagName)
      },

      sendApm(metricsValue: IFlushApmPayload, metricsName: string) {
        const metrics: {
          [props: string]: string | number | object
        } = {}
        metrics[metricsName] = {
          type: metricsName,
          value: metricsValue,
        }

        const payload = {
          frontApmTracker: {
            type: 'FrontApmTracker',
            value: metrics,
          },
        }

        if (isUpgrade(disableApm)) {
          window.insight?.push(payload)
          return
        }

        (eagletTrack.push as IPushFunction)(payload, 'ApmTracker')
      },

      sendCustomError(err, options) {
        const result = parseError(err, options)

        if (result) {
          const metrics: Tracker = {
            measurement_name: 'infraJsError',
            measurement_data: {
              type: 'custom',
              ...result,
            },
          }

          if (isUpgrade(disableApm)) {
            window.insight?.push(metrics)
            return
          }

          eagletTrack.push(metrics, 'ApmXrayTracker')
        }
      },

      checkBlankScreenError(options = {}) {
        if (isUpgrade(disableApm)) {
          window.insight?.checkBlankScreenError(options)
          return
        }
        const config = { ...blankScreenErrorConfig, ...options }
        checkBlankScreenError(eagletTrack, config)
      },

      // 
      updateUserInfo(value) {
        setUserId(extendData, value)
      },
    }
    //  insight sdk eaglet  js 
    if (
      (jsError === true || typeof jsError === 'object')
      && process.env.BUILD_ENV === 'production'
      && !window.insight
      && !disableApm
    ) {
      createJsErrorCollector(app, Launcher, eagletTrack, jsError)
    }

    if (autoCheckBS && !window.insight && !disableApm) {
      setTimeout(() => {
        checkBlankScreenError(eagletTrack, blankScreenErrorConfig)
      }, autoCheckBSTime)
    }

    if (isFirstEnter) {
      //  PE 
      createPageDurationCollector(app, eagletTrack, trackersReady, {
        ...options,
        trackPageView: this.trackPageView,
      })

      if (!window.insight && !disableApm) {
        createPerformanceCollector(eagletTrack, {
          enableResponseData: performance?.enableResponseData ?? enableResponseData,
        })
      }
    }

    if (processQueue.length) {
      // processQueue 
      //@ts-ignore
      processQueue.forEach(({ type, params }) => eagletTrack[type](...params))
    }

    if (apmTracker !== false) {
      Object.defineProperty(eagletTrack, 'perf', {
        value: createPerfUtils(eagletTrack),
      })
    }

    // Configure before any event firing
    // useful for ssr or config before pageview occurred
    if (options.extend && typeof options.extend === 'object') {
      (eagletTrack.extend as INewExtendFunction)(options.extend)
    }

    //  vue  window 
    window.eaglet = eagletTrack

    //  Vue2  Vue3
    if (Launcher.version?.startsWith('3')) {
      app.vm.config.globalProperties.$eaglet = eagletTrack
    } else {
      // @ts-ignore reason:  vue2 
      Launcher.Vue.use({
        install(Vue: Launcher2['Vue']) {
          Vue.prototype.$eaglet = eagletTrack
        },
      })
    }

    // flowSource config
    if (referPath) {
      flowSourceConfig()
    }
  },

  destroy() {
    trackerMap?.ApmTracker?.destroy()
  },
} 
export default Eaglet

export { eagletTrack as eaglet }
