import { ref, Ref } from 'vue'
import { useLazy } from '../../../composables'
import { LoadingEnum, ObserverOptions } from '../interface'

function useLazyload(enableLazyLoad: boolean, loading?: LoadingEnum, observerOptions?: ObserverOptions):{
  lazyload: Ref<boolean>
  eleContainer: Ref<HTMLElement | undefined>
} {
  const eleContainer = ref<HTMLElement>()

  //  lazyload
  const lazyload = ref<boolean>((() => {
    // props.lazyLoadfalse
    if (!enableLazyLoad) {
      return false
    }
    if (typeof window === 'undefined') {
      return false
    }

    //  'observer' 
    if (loading === 'observer') {
      return true
    }

    //  loading="lazy"
    if ('loading' in window.HTMLImageElement?.prototype) {
      return false
    }

    //  props.loading
    return enableLazyLoad || loading === 'lazy'
  })())

  if (lazyload.value) {
    // @ts-ignore
    useLazy(eleContainer, {
      ...observerOptions,
      onShow: () => {
        lazyload.value = false
      },
    })
  }
  return { lazyload, eleContainer }
}

export default useLazyload
