/**
 * 
 * @param platformType 
 * @param bridgeName bridge
 * @param timing     bridge
 */
const getEaglet = () => (process.env.BROWSER ? window.eaglet : global.eaglet);
//  1%  todo: 
function shouldLog() {
    const loggingProbability = 0.01;
    return Math.random() < loggingProbability;
}
const excludedBridgeName = ['getABFlag', 'addApmReferV2'];
export function addMeasure(options) {
    // 
    if (process.env.BUILD_ENV !== 'production' || excludedBridgeName.includes(options.bridgeName))
        return;
    //  window  global  eaglet 
    const eaglet = getEaglet();
    if (!eaglet)
        return;
    //  1%
    if (!options.isError && !shouldLog())
        return;
    //  ApmXrayTracker
    try {
        eaglet.push({
            measurement_name: 'bridgeMonitor',
            measurement_data: {
                bridgeName: options.bridgeName,
                bridgeLogType: options.isError ? 'error' : 'timing',
                bridgePlatform: options.platformType,
                ...(options.isError ? { bridgeError: options.errorInfo } : { bridgeTiming: options.timing }),
            },
        }, 'ApmXrayTracker').catch(() => {
            //  eaglet  ApmXrayTracker
        });
    }
    catch {
        //  eaglet  ApmXrayTracker
    }
}
