import type { ITrackerExtendDataPayload, ITrackerContent } from '../types/plugin'
// pv pe  initiatorType 
export default function initiatorTypeProcess(data: ITrackerExtendDataPayload, builder: ITrackerContent['builder']) {
  const isProtobufHasInitiatorType = builder?.Page?.prototype?.setInitiatorType && builder?.Page?.prototype?.getInitiatorType
  if (!isProtobufHasInitiatorType) {
    delete data?.page?.value?.initiatorType
  }
}
